.container {
  position: relative;
  background: ghostwhite;
  background-size: cover;
  background-repeat: none;
  background-position: center;
  height: 200rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
}
.left {
  width: 50%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.right {
  width: 50%;
  padding: 2rem;
  z-index: 100;
}
header {
  font-family: "Orbitron", sans-serif;
}
.header2 {
  padding: 4rem;
  font-family: "Orbitron", sans-serif;
  background: rgba(248, 248, 255, 0.356);
  margin: 2rem;
  margin-left: 4rem;
}
.title {
  text-transform: uppercase;
  font-size: 5rem;
  font-weight: 200;
  letter-spacing: 5px;
  z-index: 100 !important;
  margin-bottom: 4rem;
}
.secondarytitle {
  font-size: 1.3rem;

  margin-bottom: 2rem;
}
.phase1img {
  height: 50rem;
  display: block;
  margin: 0 auto;
  z-index: 100 !important;
}

.block {
  position: absolute;
  height: 90%;
  width: 90%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 0;
  -webkit-clip-path: polygon(100% 56%, 0 0, 0 100%);
  clip-path: polygon(100% 56%, 0 0, 0 100%);
}
.phase2img {
  margin: 0 auto;
  box-shadow: 5px 10px 18px #888888;
  width: 60rem;
  display: block;
  -webkit-clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 20%
  );
  clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 30%
  );
}
.header3 {
  padding: 4rem;
  font-family: "Orbitron", sans-serif;
  background: rgba(248, 248, 255, 0.356);
  margin: 2rem;
}
.phase3img {
  box-shadow: 5px 10px 18px #888888;
  margin: 0 auto;
  width: 60rem;
  display: block;
  -webkit-clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 20%
  );
  clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 30%
  );
}
.phase4img {
  box-shadow: 5px 10px 18px #888888;
  margin: 0 auto;
  width: 60rem;
  display: block;
  -webkit-clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 20%
  );
  clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 30%
  );
}

.special {
  filter: drop-shadow(5px 10px 2rem rgba(0, 0, 0, 0.5));
}
@media only screen and (max-width: 1600px) {
  .section {
    flex-direction: column;
  }
  .container {
    height: 370rem;
  }
  .section:nth-child(even) {
    flex-direction: column-reverse;
  }
}
@media only screen and (max-width: 1200px) {
  .left {
    width: 90%;
  }
  .right {
    width: 90%;
  }
}
@media only screen and (max-width: 826px) {
  .phase1img {
    width: 50rem;
    height: 40rem;
  }
}
@media only screen and (max-width: 726px) {
  .phase2img {
    width: 40rem;
  }
  .phase3img {
    width: 40rem;
  }
  .phase4img {
    width: 40rem;
  }
  .phase1img {
    width: 40rem;
    height: 30rem;
  }
  .header2 {
    width: 100%;
    margin: 0rem;
    padding: 2rem;
  }
}
@media only screen and (max-width: 450px) {
  .title {
    font-size: 3rem;
  }
  .phase2img {
    width: 30rem;
  }
  .phase3img {
    width: 30rem;
  }
  .phase4img {
    width: 30rem;
  }
  .phase1img {
    width: 30rem;
    height: 20rem;
  }
  .header2 {
    width: 100%;
    margin: 0rem;
    padding: 2rem;
  }
  .header3 {
    width: 100%;
    margin: 0;
  }
}
