.container {
  position: relative;
  background: ghostwhite;
  background-size: cover;
  background-repeat: none;
  background-position: center;
  height: 130rem;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Orbitron", sans-serif;
}
.block {
  position: absolute;
  top: -8rem;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 0;
  -webkit-clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}
.section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
.left {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.special {
  filter: drop-shadow(5px 10px 2rem rgba(0, 0, 0, 0.5));
}
.right {
  width: auto;
  z-index: 100;
  filter: drop-shadow(5px 10px 2rem rgba(0, 0, 0, 0.5));
}
.words {
  width: 80%;
  z-index: 100;
  margin: 0 auto;
  padding: 2rem;
  margin: 2rem;
  background-color: rgba(248, 248, 255, 0.356);
  margin-bottom: 4rem;
}
.legs {
  box-shadow: 5px 10px 18px #888888;
  z-index: 100;
  margin: 0 auto;
  display: block;
  height: 90rem;
  -webkit-clip-path: polygon(25% 0, 100% 0, 100% 75%, 75% 100%, 0 100%, 0% 25%);
  clip-path: polygon(25% 0, 100% 0, 100% 75%, 75% 100%, 0 100%, 0% 25%);
}
.arms {
  width: 60rem;
  margin: 0 auto;
  display: block;
  z-index: 100;
}
.title {
  text-transform: uppercase;
  font-size: 5rem;
  white-space: nowrap;
  font-weight: 200;
  letter-spacing: 5px;
  z-index: 100 !important;
  padding: 4rem;
  background: rgba(248, 248, 255, 0.356);
  margin-bottom: 10rem;
}
.secondarytitle {
  font-size: 1.3rem;

  margin-bottom: 2rem;
}
@media only screen and (max-width: 1250px) {
  .container {
    height: 230rem;
  }
  .section {
    flex-direction: column;
    align-items: center;
  }
  .legs {
    margin-top: 4rem;
  }
  .words {
    width: 100%;
    margin: 0;
  }
  .left {
    width: 90%;
  }
}
@media only screen and (max-width: 650px) {
  .container {
    height: 220rem;
  }
  .arms {
    margin-top: 3rem;
    width: 40rem;
  }
  .legs {
    width: 40rem;
    height: 80rem;
  }
}
@media only screen and (max-width: 450px) {
  .container {
    height: 240rem;
  }
  .title {
    font-size: 3rem;
  }
  .arms {
    width: 30rem;
  }
  .legs {
    width: 30rem;
  }
}
